'use client';

import React, { useState } from 'react';
import {
  Badge,
  IconButton,
  Popover,
  Paper,
  Typography,
  Box,
  Button,
  Chip,
  Divider,
  Alert,
  CircularProgress
} from '@mui/material';
import {
  Event as EventIcon,
  Restaurant as RestaurantIcon,
  AccessTime as AccessTimeIcon,
  People as PeopleIcon,
  TableRestaurant as TableRestaurantIcon,
  Check as CheckIcon,
  Cancel as CancelIcon,
  ShoppingCart as ShoppingCartIcon
} from '@mui/icons-material';
import { useUserActiveReservations, useConfirmReservation, useCancelReservation } from '@/lib/api/hooks';
import type { ReservationResponseDto } from '@/lib/api/reservations';

// –¶–≤–µ—Ç–∞ –¥–ª—è –∞–∫—Ç–∏–≤–Ω—ã—Ö —Å—Ç–∞—Ç—É—Å–æ–≤ (—Ç–æ–ª—å–∫–æ —Ç–µ, —á—Ç–æ –æ—Ç–æ–±—Ä–∞–∂–∞—é—Ç—Å—è –≤ header)
const getStatusColor = (status: string) => {
  switch (status) {
    case 'unconfirmed':
      return 'warning'; // –ñ–µ–ª—Ç—ã–π - —Ç—Ä–µ–±—É–µ—Ç –¥–µ–π—Å—Ç–≤–∏—è
    case 'confirmed':
      return 'success'; // –ó–µ–ª–µ–Ω—ã–π - –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–æ
    case 'started':
      return 'primary'; // –°–∏–Ω–∏–π - –º–æ–∂–Ω–æ –∑–∞–∫–∞–∑—ã–≤–∞—Ç—å
    default:
      return 'default';
  }
};

// –ü–µ—Ä–µ–≤–æ–¥ –∞–∫—Ç–∏–≤–Ω—ã—Ö —Å—Ç–∞—Ç—É—Å–æ–≤
const getStatusText = (status: string) => {
  switch (status) {
    case 'unconfirmed':
      return '–ù–µ–ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–Ω–æ–µ';
    case 'confirmed':
      return '–ü–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–æ';
    case 'started':
      return '–ù–∞—á–∞—Ç–æ';
    default:
      return status;
  }
};

const ActiveReservationBadge: React.FC = () => {
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const { data: activeReservations = [], isLoading } = useUserActiveReservations();
  const confirmMutation = useConfirmReservation();
  const cancelMutation = useCancelReservation();

  // –ü–æ–∫–∞–∑—ã–≤–∞–µ–º —Ç–æ–ª—å–∫–æ –ø–µ—Ä–≤–æ–µ –∞–∫—Ç–∏–≤–Ω–æ–µ –±—Ä–æ–Ω–∏—Ä–æ–≤–∞–Ω–∏–µ (–ø–æ –±–∏–∑–Ω–µ—Å-–ª–æ–≥–∏–∫–µ –º–æ–∂–µ—Ç –±—ã—Ç—å —Ç–æ–ª—å–∫–æ –æ–¥–Ω–æ)
  // –§–∏–ª—å—Ç—Ä—É–µ–º —Ç–æ–ª—å–∫–æ –¥–µ–π—Å—Ç–≤–∏—Ç–µ–ª—å–Ω–æ –∞–∫—Ç–∏–≤–Ω—ã–µ —Å—Ç–∞—Ç—É—Å—ã –¥–ª—è –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è –≤ header
  const displayableReservation = activeReservations.find(reservation => 
    ['unconfirmed', 'confirmed', 'started'].includes(reservation.reservation_status)
  );

  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleConfirm = async () => {
    if (displayableReservation) {
      try {
        await confirmMutation.mutateAsync(displayableReservation.reservation_id);
        handleClose();
      } catch (error) {
        console.error('Failed to confirm reservation:', error);
      }
    }
  };

  const handleCancel = async () => {
    if (displayableReservation) {
      try {
        await cancelMutation.mutateAsync(displayableReservation.reservation_id);
        handleClose();
      } catch (error) {
        console.error('Failed to cancel reservation:', error);
      }
    }
  };

  // –ï—Å–ª–∏ –Ω–µ—Ç –∞–∫—Ç–∏–≤–Ω—ã—Ö –±—Ä–æ–Ω–∏—Ä–æ–≤–∞–Ω–∏–π –¥–ª—è –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è, –Ω–µ –ø–æ–∫–∞–∑—ã–≤–∞–µ–º –Ω–∏—á–µ–≥–æ
  if (isLoading || !displayableReservation) {
    return null;
  }

  const open = Boolean(anchorEl);

  return (
    <>
      <IconButton
        color="inherit"
        onClick={handleClick}
        sx={{
          '&:hover': {
            backgroundColor: 'rgba(255, 255, 255, 0.2)',
          },
        }}
      >
        <Badge
          color={getStatusColor(displayableReservation.reservation_status) as any}
          variant="dot"
        >
          <EventIcon />
        </Badge>
      </IconButton>

      <Popover
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'right',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
      >
        <Paper sx={{ p: 3, maxWidth: 400 }}>
          <Typography variant="h6" gutterBottom>
            –ê–∫—Ç–∏–≤–Ω–æ–µ –±—Ä–æ–Ω–∏—Ä–æ–≤–∞–Ω–∏–µ
          </Typography>

          <Box sx={{ mb: 2 }}>
            <Chip
              label={getStatusText(displayableReservation.reservation_status)}
              color={getStatusColor(displayableReservation.reservation_status) as any}
              size="small"
            />
          </Box>

          <Box sx={{ mb: 2 }}>
            <Typography variant="body2" sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
              <RestaurantIcon sx={{ mr: 1, fontSize: 16 }} />
              {displayableReservation.restaurant_name}
            </Typography>
            
            <Typography variant="body2" sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
              <EventIcon sx={{ mr: 1, fontSize: 16 }} />
              {displayableReservation.reservation_date}
            </Typography>
            
            <Typography variant="body2" sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
              <AccessTimeIcon sx={{ mr: 1, fontSize: 16 }} />
              {displayableReservation.reservation_time} ({displayableReservation.duration_hours} —á.)
            </Typography>
            
            <Typography variant="body2" sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
              <PeopleIcon sx={{ mr: 1, fontSize: 16 }} />
              {displayableReservation.guests_count} —á–µ–ª–æ–≤–µ–∫(–∞)
            </Typography>
            
            <Typography variant="body2" sx={{ display: 'flex', alignItems: 'center' }}>
              <TableRestaurantIcon sx={{ mr: 1, fontSize: 16 }} />
              –°—Ç–æ–ª–∏–∫ ‚Ññ{displayableReservation.table_number}
            </Typography>
          </Box>

          {/* –û—Ç–æ–±—Ä–∞–∂–µ–Ω–∏–µ –æ—à–∏–±–æ–∫ */}
          {(confirmMutation.error || cancelMutation.error) && (
            <Alert severity="error" sx={{ mb: 2 }}>
              {(confirmMutation.error as any)?.message || 
               (cancelMutation.error as any)?.message ||
               '–ü—Ä–æ–∏–∑–æ—à–ª–∞ –æ—à–∏–±–∫–∞'}
            </Alert>
          )}

          <Divider sx={{ my: 2 }} />

          {/* –ö–Ω–æ–ø–∫–∏ –¥–µ–π—Å—Ç–≤–∏–π –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç —Å—Ç–∞—Ç—É—Å–∞ */}
          <Box sx={{ display: 'flex', gap: 1, flexWrap: 'wrap' }}>
            {displayableReservation.reservation_status === 'unconfirmed' && (
              <>
                <Button
                  variant="contained"
                  color="success"
                  startIcon={confirmMutation.isPending ? <CircularProgress size={16} /> : <CheckIcon />}
                  onClick={handleConfirm}
                  disabled={confirmMutation.isPending || cancelMutation.isPending}
                  size="small"
                >
                  –ü–æ–¥—Ç–≤–µ—Ä–¥–∏—Ç—å
                </Button>
                
                <Button
                  variant="outlined"
                  color="error"
                  startIcon={cancelMutation.isPending ? <CircularProgress size={16} /> : <CancelIcon />}
                  onClick={handleCancel}
                  disabled={confirmMutation.isPending || cancelMutation.isPending}
                  size="small"
                >
                  –û—Ç–º–µ–Ω–∏—Ç—å
                </Button>
              </>
            )}

            {displayableReservation.reservation_status === 'started' && (
              <Button
                variant="contained"
                color="primary"
                startIcon={<ShoppingCartIcon />}
                size="small"
                disabled // –ü–æ–∫–∞ –±–µ–∑ —Ñ—É–Ω–∫—Ü–∏–æ–Ω–∞–ª–∞
              >
                –°–¥–µ–ª–∞—Ç—å –∑–∞–∫–∞–∑
              </Button>
            )}

            {displayableReservation.reservation_status === 'confirmed' && (
              <Typography variant="body2" color="text.secondary" sx={{ fontStyle: 'italic' }}>
                –ë—Ä–æ–Ω–∏—Ä–æ–≤–∞–Ω–∏–µ –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–æ
              </Typography>
            )}
          </Box>
        </Paper>
      </Popover>
    </>
  );
};

export default ActiveReservationBadge;