import { api } from './client';

export type OrderType = 'delivery' | 'dine_in';

export interface CreateOrderDto {
  order_type: OrderType;
  delivery_country?: string;
  delivery_city?: string;
  delivery_street_address?: string;
  delivery_phone?: string;
  reservation_id?: string;
  should_update_user_address?: boolean;
}

export interface OrderItemResponseDto {
  order_item_id: number;
  item_id: number;
  item_name: string;
  quantity: number;
  unit_price: number;
  total_price: number;
}

export interface OrderResponseDto {
  order_id: string;
  user_id: string;
  order_type: OrderType;
  delivery_country?: string;
  delivery_city?: string;
  delivery_street_address?: string;
  delivery_phone?: string;
  reservation_id?: string;
  subtotal: number;
  delivery_fee: number;
  total_amount: number;
  created_at: string;
  updated_at: string;
  order_items?: OrderItemResponseDto[];
}

export interface UserAddressDto {
  country?: string;
  city?: string;
  street_address?: string;
}

// API —Ñ—É–Ω–∫—Ü–∏–∏
export const ordersApi = {
  // –°–æ–∑–¥–∞–Ω–∏–µ –∑–∞–∫–∞–∑–∞
  createOrder: (data: CreateOrderDto): Promise<OrderResponseDto> =>
    api.post('/orders', data),

  // –ü–æ–ª—É—á–µ–Ω–∏–µ –∑–∞–∫–∞–∑–æ–≤ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
  getUserOrders: (): Promise<OrderResponseDto[]> =>
    api.get('/orders/my'),

  // –ü–æ–ª—É—á–µ–Ω–∏–µ –∫–æ–Ω–∫—Ä–µ—Ç–Ω–æ–≥–æ –∑–∞–∫–∞–∑–∞
  getOrder: (orderId: string): Promise<OrderResponseDto> =>
    api.get(`/orders/${orderId}`),

  // –ü–æ–ª—É—á–µ–Ω–∏–µ –∞–¥—Ä–µ—Å–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –¥–ª—è –∞–≤—Ç–æ–∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è
  getUserAddress: (): Promise<UserAddressDto | null> =>
    api.get('/orders/my-address'),
};